
.github-fork-ribbon {
  position: fixed;
  top: 0;
  right: 0;
  width: 12.1em;
  height: 12.1em;
  overflow: hidden;
  z-index: 999;
  pointer-events: none;
  font-size: 13px;
  text-decoration: none;
  text-indent: -999999px;
}
.github-fork-ribbon.fixed {
  position: fixed;
}
.github-fork-ribbon:before,
.github-fork-ribbon:after {
  position: absolute;
  display: block;
  width: 15.38em;
  height: 1.54em;
  top: 3.23em;
  right: -3.23em;
  box-sizing: content-box;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.github-fork-ribbon:before {
  content: "";
  padding: 0.38em 0;
  background-color: #42b983;
  background-image: -webkit-linear-gradient(top, rgba(0,0,0,0), rgba(0,0,0,0.15));
  background-image: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.15));
/* Add a drop shadow */
  box-shadow: 0 0.15em 0.23em 0 rgba(0,0,0,0.5);
  pointer-events: auto;
}
.github-fork-ribbon:after {
/* Set the text from the title attribute */
  content: attr(title);
/* Set the text properties */
  color: #fff;
  font: 700 1em "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 1.54em;
  text-decoration: none;
  text-shadow: 0 -0.08em rgba(0,0,0,0.5);
  text-align: center;
  text-indent: 0;
/* Set the layout properties */
  padding: 0.15em 0;
  margin: 0.15em 0;
/* Add "stitching" effect */
  border-width: 0.08em 0;
  border-style: dotted;
  border-color: #fff;
  border-color: rgba(255,255,255,0.7);
}
.github-fork-ribbon.left-top:before,
.github-fork-ribbon.left-top:after {
  right: auto;
  left: -3.23em;
}
.github-fork-ribbon.left-top:before,
.github-fork-ribbon.left-top:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
*,
:after,
:before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
html {
  background: #eee;
}
body {
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
}
a {
  -webkit-text-decoration-skip: objects;
  color: #42b983;
  background: 0 0;
  cursor: pointer;
  -webkit-transition: color 0.2s ease;
  transition: color 0.2s ease;
  text-decoration: none;
}
a:active,
a:hover {
  outline: 0;
  text-decoration: none;
}
ol,
ul {
  list-style: none;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.wrapper {
  background: #eee;
  padding: 1px;
}
.wrapper .logo {
  margin: 10px auto;
  height: 80px;
  text-align: center;
}
.wrapper .logo img {
  width: 80px;
  -webkit-transition: all 0.7s cubic-bezier(0, 0.51, 0.69, 1.15);
  transition: all 0.7s cubic-bezier(0, 0.51, 0.69, 1.15);
  -webkit-transform: rotate(0) scale(10);
          transform: rotate(0) scale(10);
  -webkit-transform-origin: center center;
          transform-origin: center center;
  opacity: 0;
}
.wrapper .logo img.load {
  -webkit-transform: rotate(1080deg) scale(1);
          transform: rotate(1080deg) scale(1);
  opacity: 1;
}
.wrapper .ui-name {
  font-size: 2.2rem;
  text-align: center;
}
.wrapper .ui-name i {
  font-style: normal;
}
.wrapper .ui-name i:nth-child(1) {
  color: #f06;
}
.wrapper .ui-name i:nth-child(2) {
  color: #0089ce;
}
.wrapper .ui-name i:nth-child(3) {
  color: #f1d800;
}
.wrapper .ui-name i:nth-child(4) {
  color: #00d39a;
}
.wrapper .ui-name i:nth-child(5) {
  color: #94e827;
}
.wrapper .wrapper-container {
  background: #fcfcfc;
  width: 90%;
  margin: 10px auto 20px;
  border-radius: 6px;
  box-shadow: 0 1px 1px 0 rgba(0,0,0,0.1);
  overflow: hidden;
}
.wrapper .wrapper-content {
  padding: 20px;
}
.wrapper .wrapper-content .demo-link {
  font-size: 1rem;
  color: #42b983;
}
.wrapper .wrapper-content .demo-link:hover {
  text-decoration: underline;
}
.wrapper .wrapper-content table {
  margin-bottom: 30px;
}
.wrapper .wrapper-content code {
  width: 100%;
  margin-bottom: 20px;
  background: #e1e1e1;
  font-size: 1rem;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.wrapper .wrapper-content code:hover {
  box-shadow: 0 6px 10px 0 rgba(0,0,0,0.14), 0 1px 18px 0 rgba(0,0,0,0.12), 0 3px 5px -1px rgba(0,0,0,0.3);
}
@media (max-width: 600px) {
.wrapper .logo {
    margin: 10px 0;
    height: 40px;
}
.wrapper .logo img {
    width: 40px;
}
.wrapper .ui-name {
    font-size: 1.7rem;
}
.wrapper .wrapper-container {
    width: 98%;
    margin: 15px 0 10px;
}
.wrapper .wrapper-navigate li {
    text-align: center;
}
.wrapper .wrapper-navigate li div {
    font-size: 13.5px;
    text-align: left;
}
.wrapper .wrapper-navigate li a {
    padding-left: 5px;
    font-size: 12px;
}
.wrapper .wrapper-navigate li a .icon {
    margin: auto;
    width: 20px;
}
.wrapper .wrapper-navigate li a span {
    display: none;
}
}


