
nav {
  font-size: 14px;
  padding: 15px;
}
nav a {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  position: relative;
  padding: 10px 0 10px 10px;
  color: #525558;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
nav a.v-link-active,
nav a:hover,
nav a.active {
  color: #42b983;
}
nav a .material-icons {
  margin-right: 5px;
  min-width: 20px;
  font-size: 20px;
}
nav a span {
  line-height: 24px;
}
nav div {
  font-size: 16px;
  margin: 8px 0;
}
@media (max-width: 700px) {
nav {
    padding: 0;
}
nav a {
    font-size: 12px;
}
}


