
.markup {
  position: relative;
}
.markup:after {
  position: absolute;
  right: 1rem;
  -webkit-transition: opacity 0.2s ease-in;
  transition: opacity 0.2s ease-in;
  content: attr(data-lang);
  color: rgba(0,0,0,0.3);
  font-size: 1rem;
  font-weight: 700;
  top: 0.5rem;
}
.hljs {
  color: #424242;
}
.hljs .hljs-tag,
.hljs .hljs-variable {
  color: #ab47bc;
}
.hljs .hljs-attr,
.hljs .hljs-keyword {
  color: #2196f3;
}
.hljs .hljs-literal,
.hljs .hljs-number,
.hljs .hljs-string {
  color: #e57373;
}


